"use client";

import { useState } from "react";
import Galaxia from "../img/galaxia.jpg";
import Image from "next/image";

export default function BlogHome() {
  const [categoriaSeleccionada, setCategoriaSeleccionada] = useState(null);
  const [etiquetaSeleccionada, setEtiquetaSeleccionada] = useState(null);

  useEffect(() => {
    if (interna === "1") {
      router.push("/");
      return; // Detener el fetch
    }

    const fetchBlogData = async () => {
      try {
        const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/json/node/${interna}`, { 
          method: "GET" 
        });
        
        if (!response.ok) {
          throw new Error(`Error en la respuesta: ${response.status}`);
        }

        const data = await response.json();
        setBlogData(data);
      } catch (err) {
        console.error("Error al obtener los datos del blog:", err);
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchBlogData();
  }, []);


  const handleCategoriaClick = (categoria) => {
    setCategoriaSeleccionada((prev) => (prev === categoria ? null : categoria));
  };

  const handleEtiquetaClick = (etiqueta) => {
    setEtiquetaSeleccionada((prev) => (prev === etiqueta ? null : etiqueta));
  };

  const mostrarTodas = () => {
    setCategoriaSeleccionada(null);
  };

  const articulosFiltrados = articulos.filter(
    (articulo) =>
      (!categoriaSeleccionada || articulo.categoria === categoriaSeleccionada) &&
      (!etiquetaSeleccionada || articulo.etiqueta === etiquetaSeleccionada)
  );

  return (
    <div className="principal-blog">
      <div className="principal-blog-header">
        <div className="principal-blog-header-items">
          <div className="principal-blog-header-title">
            <h1 className="principal-blog-title">Blog</h1>
            <h2 className="principal-blog-subtitle">
              Nuestro conocimiento en tus manos
            </h2>
          </div>
          <div className="principal-blog-breadcrumb"></div>
        </div>
      </div>
      <div className="principal-blog-labels-and-articles">
        <div className="principal-blog-labels">
          <div className="principal-blog-labels-categories">
            <h1 className="principal-blog-labels-categories-title">Categorias</h1>
            <div className="principal-blog-labels-categories-items">
              {categorias.map((cat, index) => (
                <div
                  key={index}
                  className={`principal-blog-label-categories-item ${
                    categoriaSeleccionada === cat ? "active" : ""
                  }`}
                >
                  <a
                    className="principal-blog-label-categories-link"
                    onClick={() => handleCategoriaClick(cat)}
                    style={{ cursor: "pointer" }}
                  >
                    {cat}
                  </a>
                </div>
              ))}
              <div className="principal-blog-label-categories-item-all">
                <a
                  className="principal-blog-label-categories-link"
                  onClick={mostrarTodas}
                  style={{ cursor: "pointer" }}
                >
                  Todas las categorias
                </a>
              </div>
            </div>
          </div>
          <div className="principal-blog-labels-tag">
            <h1 className="principal-blog-labels-tag-title">Etiquetas</h1>
            <div className="principal-blog-labels-tag-items">
              {etiquetas.map((tag, index) => (
                <div
                  key={index}
                  className={`principal-blog-label-tag-item ${
                    etiquetaSeleccionada === tag ? "active" : ""
                  }`}
                >
                  <a
                    className="principal-blog-label-tag-link"
                    onClick={() => handleEtiquetaClick(tag)}
                    style={{ cursor: "pointer" }}
                  >
                    {tag}
                  </a>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="principal-blog-articles">
          {articulosFiltrados.length === 0 && (
            <div className="principal-blog-no-articles">
              <p>No hay artículos disponibles para esta categoría.</p>
            </div>
          )}
          {articulosFiltrados.map((articulo) => (
            <a key={articulo.id} className="principal-blog-article">
              <div className="principal-blog-article-title">
                {articulo.titulo}
              </div>
              <div className="principal-blog-article-image">
                <Image
                  className="principal-blog-image"
                  src={articulo.imagen}
                  alt={articulo.titulo}
                  width={200}
                  height={64}
                  priority
                />
                <div className="principal-blog-article-date">
                  <p>{articulo.fecha}</p>
                </div>
              </div>
              <div className="principal-blog-article-label">
                <a>{articulo.categoria}</a>
              </div>
              <div className="principal-blog-article-description">
                <p>{articulo.descripcion}</p>
              </div>
              <div className="principal-blog-article-see-more">
                <a>Ver mas</a>
              </div>
            </a>
          ))}
        </div>
      </div>
      {/* <div className="internal-blog-labels-categories-items">
              {Array.from(
                new Set(
                  blogData.map((item, index) => item?.fields?.field_categories)
                )
              ).map((category, index) => (
                <h2 key={index}>{category}</h2>
              ))}
              <div className="internal-blog-label-categories-item">
                <a className="internal-blog-label-categories-link">
                  Creatividad
                </a>
                <span className="internal-blog-label-categories-quantity">
                  -1
                </span>
              </div>
              <div className="internal-blog-label-categories-item">
                <a className="internal-blog-label-categories-link">Drupal</a>
                <span className="internal-blog-label-categories-quantity">
                  -1
                </span>
              </div>
              <div className="internal-blog-label-categories-item">
                <a className="internal-blog-label-categories-link">Github</a>
                <span className="internal-blog-label-categories-quantity">
                  -1
                </span>
              </div>
              <div className="internal-blog-label-categories-item-all">
                <a className="internal-blog-label-categories-link">
                  Todas las categorias
                </a>
              </div>
            </div> */}
    </div>
  );
}